var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
	export default {
		data() {
			return {
				name:'',
				email:'',
				imageList: [],
				sourceTypeIndex: 2,
				sourceType: ['拍照', '相册', '拍照或相册'],
				sizeTypeIndex: 2,
				sizeType: ['压缩', '原图', '压缩或原图'],
				countIndex: 8,
				count: [1, 2],
				myData:{
					nickName:'',
					gender:'',
					code:'',
					avatarUrl:''
				},
				sessionId:'',
				uploadData:[]
			}
		},
		onLoad(option){
			this.sessionId = option.sessionId
		},
		methods: {
			// 选择图片
			chooseImage: async function() {
				if (this.imageList.length === 2) {
					return;	
				}
				uni.chooseImage({
					sourceType: sourceType[this.sourceTypeIndex],
					sizeType: sizeType[this.sizeTypeIndex],
					count: this.imageList.length + this.count[this.countIndex] > 2 ? 2 - this.imageList.length : this.count[this.countIndex],
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
					},
				})
			},
			// 预览图片
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			
			// 删除选中的图片
			del(index){
				uni.showModal({
				    title: '提示',
				    content: '是否要删除该图片',
				    success: (res)=> {
				        if (res.confirm) {
							this.imageList.splice(index,1)
				        }
				    }
				});
			},
			
			// 循环上图
			async uploadImages(url){
				uni.uploadFile({
					url:'https://www.nssnail.com/file/uploadWxFile',
					filePath:url,
					name:'file',
					success: (res) => {
						var resdata = JSON.parse(res.data)
						this.uploadData.push(resdata.data)
						if(this.uploadData.length == this.imageList.length){
							uni.request({
								url:'https://www.nssnail.com/wx/saveIDImages',
								method:'POST',
								data:{
									fileList:this.uploadData,
									name:this.name,
									email:this.email,
									sessionId:this.sessionId
								},
								success: (res) => {
									if(res.data.stat == 1){
										uni.showToast({
											title:res.data.message,
											icon:'success',
											success:()=>{
												setTimeout(function(){
													uni.navigateBack({
														delta:1
													})
												},2000);
											}
										})
									}else{
										uni.showToast({
											title:res.data.message,
											icon:'none'
										})
									}
								}
							})
						}
						
					}
				})
			},
			
			// 保存信息
			async save(){
				if(this.name=='' || this.imageList.length==0){
					uni.showToast({
						title:'请补全信息',
						icon:'none'
					})
				}else{
					let arr = []
					for (let k in this.imageList) {
						arr[k] = this.uploadImages(this.imageList[k])
					}
					return arr;
				}
			},
		}
	}
